.discont {
  padding-top: 50px;
  width: 100%;
  // .discont__banner

  &__banner {
    margin-bottom: 15px;
    width: 100%;
  }

  // .discont__logo-inner

  &__logo-inner {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows:  1fr 1fr;
    grid-row-gap: 10px;
    grid-column-gap: 10px;
  }

  // .discont__logo

  &__logo {
    position: static;
    opacity: 1;
    width: 100%;
  }
}
