.pin__pas-inp[type="number"]::-webkit-outer-spin-button,
.pin__pas-inp[type="number"]::-webkit-inner-spin-button {
	display: none;
}

.pin {
  
  // .pin__title

  &__title {
    text-align: center;
    margin-bottom: 10px;
  }

  // .pin__form

  &__form {
    display: flex;
    justify-content: space-between;
    margin-bottom: 60px;
  }

  // .pin__input

  &__input {
    
  }

  // .pin__pas-inp

  &__pas-inp {
    width: 80px;
    height: 80px;
    font-size: 40px;
    text-align: center;
    @media (max-width:410px) {
      width: 65px;
      height: 65px;
    }
  }

  // .pin__number-inner

  &__number-inner {
    display: grid;
    margin: 0 auto;
    max-width: 300px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-row-gap: 25px;
    
  }

  // .pin__number

  &__number {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    margin: 0 auto;
    background-color: #F8F8F6;
    border-radius: 22px;
    cursor: pointer;
    font-size: 30px;
    font-weight: 600;
  }

  // .pin__number-cont

  &__number-cont {
    font-size: 30px;
    font-weight: 600;
  }

  // .pin__number-hide

  &__number-hide {
  }
  &__del{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
   &__del-img{
     max-width: 20px;
   }
}
