body{
  font-family: Arial,  sans-serif;
}

.stemplen {
  
  // .stemplen__container

  &__container {
    max-width: 424px;
    margin: 0 auto;
    padding: 0 25px;
    @media (max-width:450px) {
      padding: 0 5px;
    }
  }
}

.page{
  position: absolute;
    z-index: 20;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    opacity: 0;
    overflow: hidden;
    &.active{
      opacity: 1;
      z-index: 994;
      overflow: visible;
    }
}

.stemplen__sub-title{
  font-size: 16px;
  color: grey;
  font-weight: 400;
}

.burger__page{
  padding-top: 50px;
}